/* eslint-disable max-len */
import * as Yup from 'yup'

export const SignUpFormValidationSchema = Yup.object({
  email: Yup.string()
    .email(' ')
    .required(' '),
  group: Yup.string()
    .max(3, '  3 ')
    .required(' '),
  password: Yup.string()
    .min(6, '     6 ')
    .max(12, '     12 ')
    .required(' '),
})

export const SignInFormValidationSchema = Yup.object({
  email: Yup.string()
    .email(' ')
    .required(' '),
  password: Yup.string()
    .min(6, '     6 ')
    .max(12, '     12 ')
    .required(' '),
})

export const UserEditValidationSchema = Yup.object({
  name: Yup.string()
    .required(' '),
  about: Yup.string()
    .required(' '),
})

export const UserEditAvatarValidationSchema = Yup.object({
  avatar: Yup.string()
    // .matches(
    //   /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
    //   '  ',
    // )
    .required(' '),
})

export const ProductValidationSchema = Yup.object({
  available: Yup.boolean(),
  pictures: Yup.string()
    // .matches(
    //   /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
    //   '  ',
    // )
    .required(' '),
  name: Yup.string()
    .required(' '),
  price: Yup.number()
    .required(' '),
  discount: Yup.number()
    .required(' '),
  stock: Yup.number()
    .required(' '),
  wight: Yup.string()
    .required(' '),
  description: Yup.string()
    .required(' '),

})

export const CommentFormValidationSchema = Yup.object({
  text: Yup.string()
    .required(' '),
  rating: Yup.number()
    .required(' '),
})
